import React, {useState} from 'react'
import "./AddedCard.scss";
import MoreOption from "../more-option/MoreOption";
import {
  sortableHandle,
} from 'react-sortable-hoc';
import {useSelector, useDispatch} from 'react-redux';
import Modal from "../modal-component/ModalComponent";
import CardItem from "../card-item/CardItem";
import {addProductHandler, formatPrice} from 'utils/commonFunction';
import Button from "../button-component/ButtonComponent";
import EditGift from "../edit-gift/EditGift";
import GiftStatusPill from "../gift-status-pill/GiftStatusPill";
import MarkAsPurchased from "../mark-as-purchased/MarkAsPurchased";
import {CURRENCY} from "../../../../utils/constants";
import AddCashFundModal from "../cash-fund-card/add-cash-fund-modal/AddCashFundModal";

const AddedCard = (props) => {

  const dispatch = useDispatch();
  let stateGuestViewRegistry = useSelector(state => state.viewRegistry.guestViewRegistry)
  const stateRecommendedProducts = useSelector(state => state.myRegistry.recommendedProducts)
  const stateFilterApplied = useSelector(state => state.viewRegistry.viewFilterApplied.childFilter)
  const stateAddedProducts = useSelector(state => state.myRegistry.addedProducts)
  const stateRegistryInfo = useSelector(state => state.registry.info)
  const [recommendedProducts, setRecommendedProducts] = useState([])
  const [show, setShow] = useState(false);
  const modalTitle = `Recommended ${props.categoryTitle ? props.categoryTitle : props.data.registryCategories[0].category.categoryName}`
  const [showRemoveModel, setShowRemoveModel] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [updateModal, setUpdateModal] = useState(false);
  let [showMarkPurchased, setShowMarkPurchased] = useState(false)
  let [showCashFundModal, setShowCashFundModal] = useState(false)

  const showRecommendation = (e, category) => {
    e.stopPropagation()
    // Fetching all recommendations for that particular product category
    let categoryRecommended = [...stateRecommendedProducts.filter(product => Number(category.id) === Number(product.category.id))]

    // Removing duplicate products if found any
    let jsonObject = categoryRecommended.map(JSON.stringify);
    let uniqueSet = new Set(jsonObject);
    categoryRecommended = Array.from(uniqueSet).map(JSON.parse);

    setRecommendedProducts(categoryRecommended.map(product => {
      // Resetting recommended product quantity
      product.quantity = 1;
      if (product.maItemVariantItemRecommendation.length) {
        product['activeVariant'] = product.maItemVariantItemRecommendation[0]
      } else {
        product['activeVariant'] = null
      }
      return product;
    }));

    setShow(true);
  }

  const showRemoveModelPopUp = (e) => {
    e.stopPropagation()
    setShowRemoveModel(true);
  }

  const showMarkAsPurchasedModal = (e) => {
    e.stopPropagation()
    setShowMarkPurchased(true);
  }

  let moreOptionsData = []
  let addRemoveOption = false

  if (props.data.isGroupGift && props.data.quantityStatus.total_contribution_amount <= 0) {
    addRemoveOption = true
  } else if (!props.data.isGroupGift && props.data.quantityStatus.purchased_quantity + props.data.quantityStatus.reserved_quantity <= 0) {
    addRemoveOption = true
  }

  if (addRemoveOption) {
    moreOptionsData.unshift({
      name: "Remove",
      action: (e) => showRemoveModelPopUp(e)
    })
  }

  if (!props.data.isPurchased) {
    moreOptionsData.unshift({
      name: `Record a ${props.data.isGroupGift ? 'contribution' : 'purchase'}`,
      action: (e) => showMarkAsPurchasedModal(e)
    })
  }

  if (Number(props.data.registryCategories[0].category.categoryType.id) !== 2) {
    moreOptionsData.unshift({
      name: "View Recommendations",
      action: (e) => showRecommendation(e, props.data.registryCategories[0])
    })
  }


  const removeConfirmFooter = (
    <>
      <Button className='py-1 px-3 font-medium' variant='secondary' onClick={() => setShowRemoveModel(false)}>Do not
        remove</Button>
      <Button className='py-1 px-3 font-medium' variant='primary' onClick={() => {
        props.removeProductFromCartHandler();
        setShowRemoveModel(false);
      }}>Remove</Button>
    </>
  )

  // Incrementing quantity of local recommended product
  const incrementQuantity = (cardId, quantity) => {
    let newRecommended = [];
    for (let product of [...recommendedProducts]) {
      if (product.id === cardId) {
        product.quantity += quantity;
      }
      newRecommended.push(product)
    }
    setRecommendedProducts(newRecommended)
  }

  // Decrementing quantity of local recommended product
  const decrementQuantity = (cardId, quantity) => {
    let newRecommended = [];
    let updated = false;
    for (let product of [...recommendedProducts]) {
      if (product.id === cardId) {
        if (product.quantity - quantity <= 0) {
          break;
        } else {
          product.quantity -= quantity;
          updated = true;
        }
      }
      newRecommended.push(product)
    }

    if (updated) {
      setRecommendedProducts(newRecommended)
    }
  }

  // Open Edit Gift Modal
  const openEditModal = () => {
    if (props.data.registryCategories.length > 0 && props.data.registryCategories[0]?.category.categoryType.id !== "2" && !props.data.isPurchased) {
      setShowEditModal(true)
    }

    if (props.data.registryCategories.length > 0 && props.data.registryCategories[0]?.category.categoryType.id === "2" && !props.data.isPurchased) {
      setShowCashFundModal(true)
    }


  }

  // Handle drag
  // TODO: Change Draggable icon
  const DragHandle = sortableHandle(() => <i
    className={["ml-4 drag-icon m-a-popover m-a-popover-top-right", (props.disabledDrag || stateFilterApplied.length !== 0) ? "disabled-drag" : ""].join(" ")}>
    <div className="m-a-popover-content drag-drop-popover w-auto px-3 py-2">
      <p className="text-sm font-weight-normal m-a-popover-content-desc">Rearrange</p>
    </div>
  </i>);

  const setActiveRecommandationVariantHandler = (productID, variant) => {
    let newRecommended = [];
    let updated = false;
    for (let product of [...recommendedProducts]) {
      if (productID === product.id) {
        product.activeVariant = variant
        updated = true
      }
      newRecommended.push(product)
    }

    if (updated) {
      setRecommendedProducts(newRecommended)
      setUpdateModal(!updateModal)
    }
  }

  return (
    <>
      {!props.multipleCategories ?
        <>
          <div className="added-card py-5 pr-0 pr-sm-0 pr-md-2 px-2 px-md-0" onClick={openEditModal}>
            <div className="row">
              <div className="col-12 col-md-2">
                <div className="pl-0 pl-md-2">
                  {props.data?.imageUrl ?
                    <img
                      className={["product-img d-block mx-md-auto mx-0", Number(props.data.registryCategories[0].category.categoryType.id) === 2 ? "added-card-product-image-radius" : ""].join(" ")}
                      src={props.data?.imageUrl}
                      alt={props.data?.item.title}/>
                    :
                    <div className="added-card-no-product-image"/>
                  }

                </div>

              </div>
              <div className={props.draggable ? "col-12 col-md-5" : "col-12 col-md-6"}>
                {/* TODO: change category */}
                <div
                  className="text-sm category my-3 my-md-0">{props.data.registryCategories[0].category.parentCategory && props.data.registryCategories[0].category.parentCategory.parentCategory !== null ? props.data.registryCategories[0].category.parentCategory.categoryName + " (" + props.data.registryCategories[0].category.categoryName + ")" : props.data.registryCategories[0].category.categoryName}</div>
                <h2>{props.data.title}</h2>
                <div className="d-flex mt-2">
                  <div className="">{formatPrice(props.data.item.price)}</div>
                  {/*/!* TODO: Add Strikeout price dynamic *!/*/}
                  {/*<div className="text-sm my-auto ml-1 strick-price">$257.99</div>*/}
                </div>
                {!props.data.isGroupGift ?
                  props.data.quantity > 1 ?
                    <>
                      {props.data.quantityStatus.purchased_quantity > 0 ?
                        <GiftStatusPill variant="purchased"
                                        text={`${props.data.quantityStatus.purchased_quantity} Purchased`}
                                        className="d-inline-block mr-1 mt-2"/>
                        : null}
                      {props.data.quantityStatus.reserved_quantity > 0 ?
                        <GiftStatusPill variant="reserved"
                                        text={`${props.data.quantityStatus.reserved_quantity} Reserved`}
                                        className="d-inline-block mr-1 mt-2"/>
                        : null}
                      {props.data.quantityStatus.still_needed > 0 ?
                        <GiftStatusPill
                          text={`${props.data.quantityStatus.still_needed} ${props.data.quantityStatus.still_needed === props.data.quantity ? 'Requested' : 'Still Needed'}`}
                          className="d-inline-block mr-1 mt-2"/>
                        : null}
                    </>
                    :
                    <>
                      {props.data.isPurchased ?
                        <GiftStatusPill variant="purchased" text="Purchased"
                                        className="d-inline-block mr-1 mt-2"/> : null}
                      {props.data.isReserved ?
                        <GiftStatusPill variant="reserved" text="Reserved"
                                        className="d-inline-block mr-1 mt-2"/> : null}
                    </>
                  :
                  <div className="d-flex w-100 align-items-center flex-wrap">
                    <GiftStatusPill variant="group-gift"
                                    text={`Group Gift`}
                                    className="d-inline-block mr-1 mt-2"/>

                    {props.data.quantityStatus && props.data.quantityStatus.total_contribution_amount <= 0 ?
                      <GiftStatusPill
                        text={`${formatPrice(props.data.item.price * props.data.quantity, CURRENCY)} Requested`}
                        className="d-inline-block mr-1 mt-2"/>
                      :
                      props.data.isAmountReceived ?
                        <GiftStatusPill variant="purchased"
                                        text={`Received ${CURRENCY}${props.data.quantityStatus.total_contribution_amount}`}
                                        className="d-inline-block mr-1 mt-2"/>
                        :
                        <div className="flex-grow-1">
                      <span
                        className="text-sm font-medium">{`${formatPrice(props.data.quantityStatus.total_contribution_amount, CURRENCY)}`}</span>
                          <span
                            className="text-sm">{` of ${formatPrice(props.data.item.price * props.data.quantity, CURRENCY)} received`}</span>
                          <div className="added-card-group-gift-progress-bar">
                            <div className="added-card-group-gift-progress mw-100"
                                 style={{width: `${(props.data.quantityStatus.total_contribution_amount / (props.data.item.price * props.data.quantity)) * 100}%`}}/>
                          </div>
                        </div>
                    }
                  </div>
                }

              </div>
              <div className={[
                props.draggable ? "col-12 col-md-5 added-card-buttons" : "col-12 col-md-4 added-card-buttons",
              ].join(" ")}
              >
                <div className="d-flex align-items-baseline justify-content-end mr-md-1 mr-3">
                  {!stateGuestViewRegistry ?
                    <button
                      className={[
                        "mr-0 mr-sm-0 mr-md-4 d-flex align-items-center added-gift-must-have-btn px-2",
                        props.data.mostWanted ? "added-gift-must-have-active" : "added-gift-must-have-not-active"
                      ].join(" ")}
                      data-tut={"must-have-" + props.data.id}
                      onClick={props.toggleMustHave}
                    >
                      <i className={["icon mr-1 d-block", props.data.mostWanted ? "heart-fill" : "heart"].join(" ")}/>
                      <p className="text-sm added-gift-must-have-text d-none d-sm-none d-md-inline-block ">Must Have</p>
                    </button> :
                    props.data.mostWanted ?
                      <button
                        data-tut={"must-have-" + props.data.id}
                        className="mr-4 d-flex align-items-center added-gift-must-have-btn px-2 added-gift-must-have-active">
                        <i className="icon mr-1 d-block heart-fill"/>
                        <p className="text-sm added-gift-must-have-text d-none d-sm-none d-md-inline-block ">Must
                          Have</p>
                      </button>
                      : <></>
                  }
                  {!stateGuestViewRegistry && moreOptionsData.length > 0 ?
                    <MoreOption data={moreOptionsData} iconClass="icon-more-hori"/>
                    : <></>}
                  {/* TODO: Add icon/rearrange icon */}
                  {props.draggable && !stateGuestViewRegistry ?
                    <DragHandle/> : <></>}
                </div>
              </div>
            </div>
          </div>
          {props.isViewCard ?
            props.data.registryCategories.length > 1 ?
              props.data.registryCategories.map(category =>
                props.data.registryCategories[0].id !== category.id ?
                <div className="row">
                  <div className="col">
                    <div className="text-body font-medium p-6 added-card-other-category">
                      You have
                      added {props.data.item.quantity > 1 ? props.data.item.quantity : null} {category.category.parentCategory.parentCategory !== null ? category.category.parentCategory.categoryName + " (" + category.category.categoryName + ")" : category.category.categoryName} as
                      part of <a className="title-link" tabindex="0" onClick={openEditModal}>{props.data.title}</a>
                    </div>
                  </div>
                </div>
                : null
              )
              : null
          : null}
        </>
        :
        <div className="row">
          <div className="col">
            <div className="text-body font-medium p-4 p-md-6 added-card-other-category">
              You have
              added {props.data.item.quantity > 1 ? props.data.item.quantity : null} {props.multipleCategories.category.parentCategory && props.multipleCategories.category.parentCategory.parentCategory !== null ? props.multipleCategories.category.parentCategory.categoryName + " (" + props.multipleCategories.category.categoryName + ")" : props.multipleCategories.category.categoryName} as
              part of <a className="title-link" tabindex="0" onClick={openEditModal}>{props.data.title}</a>
            </div>
          </div>
        </div>
      }
      <Modal
        updateModal={updateModal}
        show={show}
        closeModal={() => setShow(false)}
        title={modalTitle}
        size="xl"
      >
        <div className="position-relative added-card-recommended">
          <div className="row category-recommended-products">
            {recommendedProducts.map((card, index) => (
              <div className="col-4 mb-5" key={"CardItem-" + index}>
                <CardItem
                  data={card}
                  incrementQuantityHandler={() => incrementQuantity(card.id, 1)}
                  decrementQuantityHandler={() => decrementQuantity(card.id, 1)}
                  activeVariant={card.activeVariant}
                  setActiveRecommandationVariantHandler={(variant) => setActiveRecommandationVariantHandler(card.id, variant)}
                  addProductHandler={() => addProductHandler(card, stateAddedProducts, stateRegistryInfo, dispatch)}
                />
              </div>
            ))}
            {recommendedProducts.length <= 0 ?
              <div className="col text-center py-4"><h4>No recommendation found</h4></div> : null}
          </div>
          {recommendedProducts.length > 0 ? <div className="added-card-recommends-bottom-gradient"/> : null}
        </div>
      </Modal>
      <Modal
        show={showRemoveModel}
        size="md"
        footer={removeConfirmFooter}
      >
        <h2>Do you want to remove {props.data.title}? </h2>
      </Modal>

      {showEditModal ?
        <EditGift
          show={showEditModal}
          closeModal={() => setShowEditModal(false)}
          data={props.data}
          openRemoveModal={setShowRemoveModel}
        />
        : null}
      <MarkAsPurchased
        show={showMarkPurchased}
        closeModal={() => setShowMarkPurchased(false)}
        data={props.data}
      />
      {showCashFundModal ?
        <AddCashFundModal
          show={showCashFundModal}
          closeModal={() => setShowCashFundModal(false)}
          categoryData={props.data}
          edit
        />
        : null}
    </>
  )
}

export default AddedCard
