export const getProducts = (registryId, dispatch) => {
  let data = {
    query: GET_PRODUCTS,
    variables: {
      registryId: parseInt(registryId)
    }
  }
  dispatch(setLoader(true))
  graphQL(data).then(res => {
    // Filter Count
    let filterCount = {
      categoryData: {...res.data.data.registryItems.categoryData},
      registryItemsAnalysisCount: {...res.data.data.registryItems.registryItemsAnalysisCount}
    }
    dispatch(setFilterProductCount(filterCount))

    let responseData = res.data.data.registryItems.registryItems.edges.map(item => {
      // return { ...item.node }
      let newItemObj = {...item.node}
      delete newItemObj.registryitemImage;
      if (item.node.registryitemImage.length > 0) {
        newItemObj.imageUrl = (MEDIA_URL_PREFIX.LOCAL_MEDIA_URL ? MEDIA_URL_PREFIX.LOCAL_MEDIA_URL : "") + item.node.registryitemImage[0].image;
      }
      return newItemObj;
    })

    // TODO: change category
    let finalData = responseData.filter(product => product.registryCategories.length > 0)
    dispatch(setRegistryProducts(finalData))
    dispatch(setLoader(false))
  }).catch(err => {
    dispatch(setLoader(false))
    console.error(err.response)
  })

  getTodoList(registryId, dispatch)
}

export const toggleMustHaveProduct = (product, dispatch) => {
  let data = {
    query: TOGGLE_MUST_HAVE,
    variables: {
      productId: product.id,
      mustHaveValue: !product.mostWanted,
      quantity: product.quantity
    }
  }
  graphQL(data).then(res => {
    if (res.status === 200) {
      dispatch(toggleMustHaveProductAction(product.item.id))
    }
  }).catch(err => {
    console.error(err.message)
  })
}

