// While using this method. Plase mak sure of parentCategory Parameter
export const getRefinedCategory = (category, parentCategory = null) => {
  return ({
    id: category.id,
    category: category.category,
    categoryName: category.category.categoryName,
    description: category.category.description,
    categoryType: category.category.categoryType,
    isDesired: category.isDesired,
    isExcluded: category.isExcluded,
    addedItemsCount: category.addedItemsCount,
    totalItemsCount: category.totalItemsCount,
    isParent: category.category.isParent,
    parentCategory: parentCategory
  })
}


// Get all categories
export const getAllCategories = (registryCategories, getAll) => {
  // let finalCondition = category.subCategoriesIsExcluded ?
  let parentCategories = registryCategories.filter(category => getAll ? true : (category.subCategoriesIsExcluded ? !category.subCategoriesIsExcluded : !category.isExcluded))
  let finalCategories = parentCategories.map(parentCategory => {
    let newParentCategory = getRefinedCategory(parentCategory)
    let subCategory = parentCategory.registrySubCategories;
    delete parentCategory.registrySubCategories
    let newSubCategories = subCategory.filter(cat => getAll ? true : (cat.subCategoriesIsExcluded ? !cat.subCategoriesIsExcluded : !cat.isExcluded))
    newSubCategories = newSubCategories.map(cat => {
      let refinedSubCate = getRefinedCategory(cat, {
        id: parentCategory.category.id,
        name: parentCategory.category.categoryName
      })
      let subProductTypes = cat.registrySubProductTypes.map(subProduct => getRefinedCategory(subProduct, {
        id: cat.category.id,
        name: cat.category.categoryName
      }))
      refinedSubCate['subProductTypes'] = subProductTypes.filter(category => getAll ? true : !category.isExcluded)
      return refinedSubCate
    })
    newParentCategory['subCategories'] = newSubCategories
    return newParentCategory
  })

  return finalCategories
}

