export const getMAVariant = async (registryId, variantId) => {
  let returnValue = null
  if (!registryId || !variantId) {
    return returnValue
  }

  let reqData = {
    query: GET_MA_VARIANT,
    variables: {
      registryId: registryId,
      variantId: variantId
    }
  }

  await graphQL(reqData).then(res => {
    if (res.data && res.data.data.maItemVariants) {
      returnValue = res.data.data.maItemVariants
    } else if (res.errors) {
      console.error("Error occurred while fetching variants", res.errors)
    }
  })

  return returnValue
}

export const formatPrice = (price, currency = CURRENCY) => {
  if (price) {
    price = String(price).replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    return `${currency}${price}`
  }
  return price
}