import React, {Component} from 'react'
import {connect} from "react-redux";
import AddedCard from "modules/commons/components/added-card/AddedCard";
import {
  toggleMustHaveProduct,
  addRecommendedProduct,
  setFilterCategory,
} from "../../MyRegistryActions";
import CheckList from "modules/my-registry/components/checkList/CheckList";
import CheckListItems from "modules/my-registry/components/checkList/CheckListItems";
import ViewRecommend from 'modules/commons/components/view-recommend/ViewRecommend';
import FilterSection from '../../components/filter-section/FilterSection';
import {graphQL, getRecommendation} from 'api/axios';
import {GET_RECOMMENDATIONS} from 'api/queries';
import {TOGGLE_MUST_HAVE, TOUR_UPDATE_FLAG} from 'api/mutations';
import {
  getProducts,
  removeProductFromCartHandler,
  getCategories,
  updateMushHaveTourFlag,
  UpdateBuildTourFlag
} from "utils/commonFunction";
import {Cookies} from "react-cookie";
import config from "api/config";
import axios from "axios";
import CashFundCard from "../../../commons/components/cash-fund-card/CashFundCard";
import Button from "../../../commons/components/button-component/ButtonComponent";
import AddCashFundModal from "../../../commons/components/cash-fund-card/add-cash-fund-modal/AddCashFundModal";
import "./Everything.scss";
import Tour from 'reactour';
import TourCard from 'modules/commons/components/tour-card/TourCard';
import {BUILD_REGISTRY_STEPS, MUST_HAVE_STEPS} from 'utils/constants';
import {setLoader} from "modules/registry/RegistryActions";
import {fetchDefaultCashFundImage} from "../../../../utils/commonFunction";
import { toast } from 'react-toastify';

class Everything extends Component {

  state = {
    showCashFundModal: false,
    cashFundProduct: null,
    isTourOpen: false,
    isMustHaveTour: false,
    steps: [],
    recommendedCashFundImage: []
  }
  checkListRef = React.createRef();
  recommendationAPICalled = false
  isRecommendationLoaderActive = false
  updateRecommendedCashFundImage = false
  toastDisplayed = false
  checkListHeightFromTop = null

  componentDidMount () {
    this.checkListHeightFromTop = null
    window.addEventListener('scroll', this.handleScroll);
    getCategories(this.props.stateRegistryInfo.id, this.props.dispatch);
    if(this.props.location.state?.getRecommendation) {
      this.setAllRecommendation(this.props.stateCheckList)
    }
  }

  openCashFundModal = () => {
    this.setState({
      showCashFundModal: true
    })
  }

  setCashFundCategoryAndOpenModal = (product) => {
    this.setState({
      cashFundProduct: {...product}
    })
    this.openCashFundModal()
  }

  closeCashFundModal = () => {
    this.setState({
      showCashFundModal: false,
      cashFundProduct: null
    })
  }

  // Updating product count in filter categories
  updateFilterList = (addedProducts) => {
    const newFilterList = [
      {
        id: "giftsAdded",
        display: "Items added",
        count: addedProducts.length,
        child: [
          {
            id: "mustHave",
            display: "Must have",
            count: addedProducts.filter(product => product.mostWanted).length
          },
          {
            id: "purchased",
            display: "Purchased",
            count: addedProducts.filter(product => product.quantityStatus && product.quantityStatus.purchased_quantity > 0).length
          },
          {
            id: "reserved",
            display: "Reserved",
            count: addedProducts.filter(product => product.quantityStatus && product.quantityStatus.reserved_quantity > 0).length
          },
          {
            id: "available",
            display: "Available",
            count: addedProducts.filter(product => !product.isPurchased && !product.isReserved).length
          }
        ]
      },
      {
        id: "giftsToAdd",
        display: "Items to add",
        count: 0
      }
    ]

    this.props.dispatch(setFilterCategory(newFilterList))
  }

  toggleMustHaveProduct = (e, product) => {
    e.stopPropagation()
    if (!this.props.stateRegistryInfo.isMustHaveApplied) {
      let steps = {...MUST_HAVE_STEPS}
      steps[0].selector = '[data-tut="must-have-' + product.id + '"]';
      steps.length = 1;
      this.setState({
        isMustHaveTour: true,
        steps: steps,
        isTourOpen: true
      })
    }

    let data = {
      query: TOGGLE_MUST_HAVE,
      variables: {
        productId: product.id,
        mustHaveValue: !product.mostWanted,
        quantity: product.quantity
      }
    }
    graphQL(data).then(res => {
      if (res.status === 200) {
        this.props.dispatch(toggleMustHaveProduct(product.item.id))
      }
    }).catch(err => {
      console.error(err.message)
    })
  }


  setAllRecommendation = async (categories) => {
    let recommendedCategory = []
    for (let parentCat of categories) {
      for (let category of parentCat.subCategories) {
        if (category.subProductTypes.length) {
          for (let subProduct of category.subProductTypes) {
            recommendedCategory.push({
              ...subProduct,
              isSubProduct: true,
              parentCategory: category.categoryName,
            })
          }
        } else {
          recommendedCategory.push({
            ...category,
            parentCategory: parentCat.categoryName,
          })
        }
      }
    }
    this.props.dispatch(setLoader(true))
    this.isRecommendationLoaderActive = true
    let data = {
      query: GET_RECOMMENDATIONS,
      variables: {
        registryId: Number(this.props.stateRegistryInfo.id),
      }
    }
    const apiURL = process.env.NODE_ENV === 'production' ? config.PRODUCTION_API_URL : config.LOCAL_API_URL;
    const cookies = new Cookies();

    await axios.get(apiURL + 'graphql', {
      params: data,
      headers: {"Authorization": cookies.get("access_token") ? "Token " + cookies.get("access_token") : ""}
    })
      .then(res => {
        let rData = []
        for (let item of [...res.data.data.itemRecommendations]) {
          let recommendationCat = [...recommendedCategory].find(cat => cat.category.id === item.category.id)
          if (recommendationCat) {
            let resData = {...item}
            resData['quantity'] = 1
            resData['category'] = recommendationCat
            if (item.maItemVariantItemRecommendation.length) {
              resData['activeVariant'] = item.maItemVariantItemRecommendation[0]
            } else {
              resData['activeVariant'] = null
            }
            rData.push(resData)
          }
        }
        if (rData.length !== 0) {
          this.props.dispatch(addRecommendedProduct(rData))
        }
        this.props.dispatch(setLoader(false))
        this.isRecommendationLoaderActive = false
      }).catch(err => {
        this.recommendationAPICalled = false
        this.props.dispatch(setLoader(false))
        this.isRecommendationLoaderActive = false
        console.error(err.message)
      })
    if(this.props.location.state?.getRecommendation) {
      let urlParams = {...this.props.location.state}
      urlParams.getRecommendation = false;
      await this.props.history.replace({
        pathname: this.props.location.pathname,
        state: urlParams
      });
    }
  }

  cashFundImageApiCalled = []

  getSortedProducts = (addedProducts, stateCategories, stateRecommendedProducts, stateFilterApplied) => {
    const cashFundCategory = stateCategories.find(category => category.categoryType && Number(category.categoryType.id) === 2)
    // for future ref : let cashFundCount = 0
    let productsToAppend = []
    if (cashFundCategory) {
      for (let subCategory of cashFundCategory.subCategories) {
        let currentCashFundImage = this.state.recommendedCashFundImage.find(el => el.categoryId === subCategory.id)

        // Preventing multiple api calls
        let imageApiCalled = this.cashFundImageApiCalled.find(el => el === subCategory.id)

        if (!currentCashFundImage && !imageApiCalled) {
          this.cashFundImageApiCalled.push(subCategory.id)
          fetchDefaultCashFundImage(subCategory.category.id).then(async res => {
            if (res.status === "success") {
              this.updateRecommendedCashFundImage = true

              await this.setState({
                recommendedCashFundImage: this.state.recommendedCashFundImage.find(el => el.categoryId === subCategory.id) ? this.state.recommendedCashFundImage : [...this.state.recommendedCashFundImage, {
                  categoryId: subCategory.id,
                  imageUrl: res.data
                }],
              })
              this.cashFundImageApiCalled = this.cashFundImageApiCalled.filter(el => el !== subCategory.id)
            }
          })
        }
        if (Number(subCategory.addedItemsCount) === 0) {
          //for future ref :  cashFundCount++;
          productsToAppend.push({
            imageUrl: currentCashFundImage && currentCashFundImage.imageUrl ? currentCashFundImage.imageUrl : "",
            registryCategories: [{
              id: subCategory.id,
              category: {
                ...subCategory
              }
            }]
          })
        }
      }
      if (!stateFilterApplied.parentFilter) {
        addedProducts = [...addedProducts, ...productsToAppend]
      }
    }

    let allProducts = []
    let productCount = 0;
    let productIds = [];
    let categories = []
    for (let parentCat of stateCategories) {
      if (!parentCat.isParent) {
        categories.push({
          id: parentCat.id,
          name: parentCat.categoryName,
          categoryName: parentCat.categoryName,
          added: parentCat.addedItemsCount,
          addedItemsCount: parentCat.addedItemsCount,
          total: parentCat.totalItemsCount,
          totalItemsCount: parentCat.totalItemsCount,
          isSubProductTypes: false,
          parentCategory: {
            id: parentCat.id,
            name: parentCat.categoryName,
            categoryName: parentCat.categoryName,
          },
          superParentCat: null,
          categoryType: parentCat.categoryType,
        })
      } else {
        for (let category of parentCat.subCategories) {
          if (category.subProductTypes.length) {
            for (let subProduct of category.subProductTypes) {
              categories.push({
                id: subProduct.id,
                name: subProduct.categoryName,
                categoryName: subProduct.categoryName,
                added: subProduct.addedItemsCount,
                addedItemsCount: subProduct.addedItemsCount,
                total: subProduct.totalItemsCount,
                totalItemsCount: subProduct.totalItemsCount,
                isSubProductTypes: true,
                isSubProduct: true,
                parentCategory: {
                  id: category.id,
                  name: category.categoryName,
                  categoryName: category.categoryName
                },
                superParentCat: {
                  id: parentCat.id,
                  name: parentCat.categoryName,
                  categoryName: parentCat.categoryName
                },
                categoryType: category.categoryType,
              })
            }
          } else {
            categories.push({
              id: category.id,
              name: category.categoryName,
              categoryName: category.categoryName,
              added: category.addedItemsCount,
              addedItemsCount: category.addedItemsCount,
              total: category.totalItemsCount,
              totalItemsCount: category.totalItemsCount,
              isSubProductTypes: false,
              parentCategory: {
                id: parentCat.id,
                name: parentCat.categoryName,
                categoryName: parentCat.categoryName,
              },
              superParentCat: null,
              categoryType: category.categoryType,
            })
          }
        }
      }
    }


    for (let category of categories) {
      let products = []
      let recommendations = []

      // Getting filtered Products and recommendations
      if (stateFilterApplied.parentFilter) {
        if (stateFilterApplied.parentFilter === "giftsAdded") {
          if (stateFilterApplied.childFilter.length > 0) {
            const childFilterArray = stateFilterApplied.childFilter

            for (let product of addedProducts) {
              let categoryFound = false
              for (let cat of product.registryCategories) {
                if (cat.id === category.id) {
                  categoryFound = true
                  break
                }
              }
              if (
                ((childFilterArray.includes("mustHave") && product.mostWanted) ||
                  (childFilterArray.includes("purchased") && product.quantityStatus && product.quantityStatus.purchased_quantity > 0) ||
                  (childFilterArray.includes("reserved") && product.quantityStatus && product.quantityStatus.reserved_quantity > 0) ||
                  (childFilterArray.includes("available") && !product.isPurchased && !product.isReserved)) && categoryFound
              ) {
                products.push(product)
                // filteredProductsId.push(product.id)
              }
            }
          } else {
            // TODO: change category
            products.push(...addedProducts.filter(el => {
              let categoryFound = false
              for (let cat of el.registryCategories) {
                if (cat.id === category.id) {
                  categoryFound = true
                  break
                }
              }
              return categoryFound;
            }))
          }
        } else if (stateFilterApplied.parentFilter === "giftsToAdd") {
          let addedRecommendations = []
          recommendations = stateRecommendedProducts.filter(el => {
            if (el.category.id === category.id && !addedRecommendations.includes(el.id)) {
              addedRecommendations.push(el.id)
              return true;
            }
            return false;
          }).map(el => {
            let recommendationProduct = {...el};
            recommendationProduct.category = category;
            return recommendationProduct;
          })
        }
      } else {
        // TODO: Change this condition(el.item.category.length)
        // TODO: change category
        products.push(...addedProducts.filter(el => {
          let categoryFound = false
          for (let cat of el.registryCategories) {
            if (cat.id === category.id) {
              categoryFound = true
              break
            }
          }
          return categoryFound && el.registryCategories.length
        }))
        let addedRecommendations = []
        recommendations = stateRecommendedProducts.filter(el => {
          if (el.category.id === category.id && !addedRecommendations.includes(el.id)) {
            addedRecommendations.push(el.id)
            return true;
          }
          return false;
        }).map(el => {
          let recommendationProduct = {...el};
          recommendationProduct.category = category;
          return recommendationProduct;
        })
      }

      if (products.length > 0) {
        for (let item of products) {
          if (!productIds.includes(item.id) && item.id !== undefined) {
            productIds.push(item.id)
          }
        }
      }

      if (stateFilterApplied.parentFilter) {
        if (stateFilterApplied.parentFilter === "giftsToAdd" && Number(category.categoryType.id) === 2) {
          let productToPush = productsToAppend.find(product => product.registryCategories[0].id === category.id)
          if (productToPush) {
            products.push(productToPush)
          }
        }
      }
      // for future ref : productCount += products.length;
      allProducts.push({
        category: category,
        products: products,
        recommendations: (category.added < category.total || category.total === 0) ? recommendations : []
      })
    }

    // Grouping products based on parent category
    let groupedProducts = []
    for (const product of allProducts) {
      let foundCategory = false;
      groupedProducts = groupedProducts.map(el => {

        if (product.category.superParentCat) {
          if (el.id === product.category.superParentCat.id) {
            foundCategory = true;
            el.allProducts.push(product);
          }
        } else if (el.id === product.category.parentCategory.id) {
          foundCategory = true;
          el.allProducts.push(product);
        }
        return el;
      })

      if (!foundCategory) {
        groupedProducts.push({
          ...product.category.parentCategory,
          categoryType: product.category.categoryType,
          allProducts: [product]
        })
      }
    }

    // Adding total products and recommendations
    groupedProducts = groupedProducts.map(el => {
      let productCount = 0;
      let recommendationCount = 0;

      for (let product of el.allProducts) {
        productCount += product.products.length;
        recommendationCount += product.recommendations.length;
      }

      el['totalProducts'] = productCount;
      el['totalRecommendations'] = recommendationCount;
      return el;
    })

    // Removing items that does not have products or recommendations based on filters
    if (stateFilterApplied.parentFilter) {
      if (stateFilterApplied.parentFilter === "giftsAdded") {
        groupedProducts = groupedProducts.filter(product => product.totalProducts > 0)
      } else if (stateFilterApplied.parentFilter === "giftsToAdd") {
        groupedProducts = groupedProducts.filter(product => {
          if (Number(product.categoryType.id) === 2) {
            return true
          }
          return product.totalRecommendations > 0
        })
      }
    } else {
      groupedProducts = groupedProducts.filter(product => (product.totalProducts > 0 || product.totalRecommendations > 0))
    }

    // for future ref :
    // if (!stateFilterApplied.parentFilter) {
    //   productCount -= cashFundCount
    // }

    productCount = productIds.length
    return {groupedProducts, productCount};
  }

  getChecklistPercentage = (checklistItems) => {
    let totalItems = 0;
    let addedItems = 0;
    for (let category of checklistItems) {
      totalItems += category.totalItemsCount;
      addedItems += category.addedItemsCount;
    }

    let result = {
      result: (addedItems / totalItems) * 100,
      addedItems: addedItems,
      totalItems: totalItems,
    }
    return result;
  }

  handleToggleEvent = () => {
    const checkListIconEl = this.checkListRef.current;
    // checkListIconEl.classList.toggle("closed");
    checkListIconEl.classList.toggle("opened");
  }

  closeTour = () => {
    // For Build tour
    if (!this.props.stateRegistryInfo.isBuildRegistryVisited && !this.state.isMustHaveTour) {
      UpdateBuildTourFlag(this.props.stateRegistryInfo, this.props.dispatch)
    }

    // For must-have
    if (this.state.isMustHaveTour && !this.props.stateRegistryInfo.isMustHaveApplied) {
      updateMushHaveTourFlag(this.props.stateRegistryInfo, this.props.dispatch)
    }

    this.setState({
      isTourOpen: false
    })
  }

  showToast = async () => {
    if(!this.toastDisplayed) {
      await toast.success(<p>{this.props.location.state.toastMessage}</p>)
      this.toastDisplayed = true
    }
    let urlParams = {...this.props.location.state}
    urlParams.toastMessage = false;
    await this.props.history.replace({
      pathname: this.props.location.pathname,
      state: urlParams
    });
  }

  handleScroll = (event) => {
    // For making it responsive
    if(document.body.offsetWidth > 768) {
      let el = document.getElementById('checklist-scrollarea');
      let registryContainer = document.getElementById('m-a-registry-container');
      if (el) {
        // Setting height from top to checklist
        if(el.getBoundingClientRect().top === 0 && !this.checkListHeightFromTop) {
          this.checkListHeightFromTop = window.scrollY
        }
        // Setting position of checklist to fixed
        if (el.getBoundingClientRect().top <= 0 && registryContainer.getBoundingClientRect().bottom > window.innerHeight)
        {
          let parentWidth = document.getElementById('checklist-container').offsetWidth;
          el.style.position = 'fixed';
          el.style.top = 0;
          el.style.paddingTop = '30px';
          el.style.width = `${parentWidth}px`;
        }
        // Setting position of checklist to static
        if (this.checkListHeightFromTop >= window.scrollY || registryContainer.getBoundingClientRect().bottom <= window.innerHeight) {
          // if bottom of the containter has reached
          if (registryContainer.getBoundingClientRect().bottom <= window.innerHeight) {
            el.style.position = 'fixed';
            el.style.top = `${(registryContainer.getBoundingClientRect().bottom % window.innerHeight) - window.innerHeight}px`;
          } else {
            el.style.position = 'static';
            el.style.top = 'auto';
          }
          el.style.paddingTop = 0;
        }
      }
    }
  }

  render() {
    if (this.props.stateRecommendedProducts.length === 0 && this.props.stateCheckList.length > 0 && !this.recommendationAPICalled) {
      this.setAllRecommendation(this.props.stateCheckList);
      this.recommendationAPICalled = true
    }

    // Recommendation Loader
    if(this.isRecommendationLoaderActive && !this.props.stateLoader && this.props.stateRecommendedProducts.length === 0) {
      this.props.dispatch(setLoader(true))
    }

    // For Build-registry tour
    if (!this.state.isMustHaveTour && !this.props.stateRegistryInfo.isBuildRegistryVisited && this.props.stateRecommendedProducts.length && !this.state.isTourOpen) {
      this.setState({
        isTourOpen: true,
        steps: BUILD_REGISTRY_STEPS
      })
    } else if (!this.state.isMustHaveTour && this.state.isTourOpen && this.props.stateRegistryInfo.isBuildRegistryVisited && this.props.stateRecommendedProducts.length) {
      this.setState({
        isTourOpen: false,
      })
    }

    // For loader
    if (this.props.stateCheckList.length <= 0) {
      this.props.dispatch(setLoader(true))
    }

    // For toast message from other route
    if(this.props.location.state?.toastMessage && !this.toastDisplayed) {
      this.showToast()
    }

    // let groupedProducts = []
    // let productCount = 0
    //
    // if (this.state.recommendedCashFundImage.length > 0 && this.updateRecommendedCashFundImage){
    //
    // }

    this.updateFilterList(this.props.stateAddedProducts)
    let {groupedProducts, productCount} = this.getSortedProducts(this.props.stateAddedProducts, this.props.stateCheckList, this.props.stateRecommendedProducts, this.props.stateFilterApplied)

    return (
      <>
        <div className="container">
          <div className="row mt-3 mt-md-6">
            {/* View CheckList */}
            <div className="col-md-12 col-lg-3 px-0 px-lg-2 checklist-container" id="checklist-container" ref={this.checkListRef}>
              <div data-tut="build-step-one" className="pr-0 pr-md-5 checklist-scrollarea" id="checklist-scrollarea">
                <div className="d-flex align-items justify-content-between px-4 px-lg-0 w-100 checklist-header">
                  <CheckList percentage={this.getChecklistPercentage(this.props.stateCheckList)}/>
                  <span className="checklist-container-icon-arrow mr-2 mr-md-0" onClick={this.handleToggleEvent}></span>
                </div>
                <div className="divider-div d-inline-block d-lg-none my-2"/>
                <div className="checklist-items-continer px-4 px-md-0">
                  {this.props.stateCheckList.map(item => <CheckListItems key={item.categoryName} item={item}
                                                                         dispatch={this.props.dispatch}/>)}
                </div>
              </div>
            </div>
            <div className="col-12 col-lg-9 mb-11 px-3" id="m-a-registry-container">

              {/* Top Filter Section */}
              <FilterSection productCount={productCount}/>
              <div className="mt-5 px-2 px-md-0">
                {groupedProducts.map(el => {
                  let hasCashFund = false;
                  return (<>
                      <h4 className="text-uppercase my-4">{el.name}</h4>
                      {el.allProducts.map((product, i) => (
                        <div id={product.category.parentCategory.name + '(' + product.category.name + ')'}>
                          {product.products.map((card, index) => {
                            if (Number(card.registryCategories[0].category.categoryType.id) === 2) {
                              hasCashFund = true
                            }
                            if (Number(card.registryCategories[0].category.categoryType.id) === 2 && Number(card.registryCategories[0].category.addedItemsCount) === 0) {
                              return (
                                <CashFundCard
                                  data={card}
                                  key={"cashFund" + index}
                                  className="mt-2"
                                  addButtonClickHandler={() => this.setCashFundCategoryAndOpenModal(card)}
                                />
                              )
                            } else {
                              return (
                                <AddedCard
                                  categoryTitle={product.recommendations.length ? product.category.isSubProductTypes ? product.category.parentCategory.name + ' (' + product.category.name + ')' : product.recommendations[0].category.categoryName : null}
                                  data={card} key={"addedCard" + index}
                                  multipleCategories={card.registryCategories.length > 1 ? card.registryCategories.find((cat, index) => Number(cat.id) === Number(product.category.id) && index != 0) : null}
                                  toggleMustHave={(ev) => this.toggleMustHaveProduct(ev, card)}
                                  removeProductFromCartHandler={() => removeProductFromCartHandler(card, this.props.stateRegistryInfo.id, this.props.dispatch)}
                                />
                              )
                            }
                          })}
                          {product.recommendations.length > 0 ?
                            <ViewRecommend
                              title={product.category.isSubProductTypes ? product.category.parentCategory.name + ' (' + product.category.name + ')' : product.recommendations[0].category.categoryName}
                              stepTwoForTour={i === 0 ? "build-step-two" : product.recommendations[0].category.id + '-' + i}
                              category={product.recommendations[0].category}
                              products={product.recommendations}/>
                            : null}
                        </div>
                      ))}
                      {hasCashFund ?
                        <div className="my-2">
                          <Button
                            variant="secondary"
                            className=" w-100 py-1 text-body font-medium"
                            onClick={this.openCashFundModal}
                          >Add my own</Button>
                        </div>
                        : null}
                    </>
                  )
                })}
              </div>
            </div>
          </div>
        </div>
        {this.state.showCashFundModal ?
          <AddCashFundModal
            show={this.state.showCashFundModal}
            closeModal={this.closeCashFundModal}
            categoryData={this.state.cashFundProduct}
          />
          : null}

          {this.state.isTourOpen ?
            <Tour
              highlightedMaskClassName='tour-bg'
              steps={this.state.steps}
              isOpen={this.state.isTourOpen}
              CustomHelper={TourCard}
              onRequestClose={this.closeTour}/>
            : null}
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    stateProducts: state.myRegistry.products,
    stateAddedProducts: state.myRegistry.addedProducts,
    stateFilterApplied: state.myRegistry.filterApplied,
    stateActiveRecommandation: state.myRegistry.activeRecommandation,
    stateRecommendedProducts: state.myRegistry.recommendedProducts,
    stateCheckList: state.myRegistry.checkList,
    stateRegistryInfo: state.registry.info,
    stateLoader: state.registry.loader,
  }
}


export default connect(
  mapStateToProps
)(Everything);
