import React from "react";
import "./ViewRegistryHeader.scss";
import Button from "../../../commons/components/button-component/ButtonComponent";
import MoreOption from "../../../commons/components/more-option/MoreOption";
import CoverImagePicker from "../../components/cover-image-picker/CoverImagePicker";
import { connect } from "react-redux";
import { toast } from 'react-toastify';
import EditViewRegistryDetail from "../../components/edit-view-registry-detail/EditViewRegistryDetail";
import { setguestViewRegistry } from "modules/view-registry/ViewRegistryActions";
import ShareRegistry from "../../components/share-registry/ShareRegistry";
import { updateGuestViewRegistry } from 'utils/commonFunction';
import {withRouter} from "react-router";

class ViewRegistryHeader extends React.Component {

  state = {
    showCoverImagePicker: false,
    showEditRegistryDetail: false,
    showShareRegistry: false
  }

  componentDidMount() {
    let queryParams = new URLSearchParams(window.location.search)
    if (queryParams.has('share')){
      this.setState({
        showShareRegistry: true
      })
      queryParams.delete('share')
      this.props.history.replace({
        search: queryParams.toString(),
      })
    }
    if (!this.props.guestViewRegistryState.urlSlug) {
      updateGuestViewRegistry(this.props.stateRegistryInfo.urlSlug, this.props.dispatch)
    }
  }

  closeCoverImagePicker = () => {
    this.setState({ showCoverImagePicker: false })
  }

  closeShareRegistry = () => {
    this.setState({ showShareRegistry: false })
  }

  closeEditRegistryDetail = () => {
    this.setState({ showEditRegistryDetail: false })
  }

  changeRegistryDetail = (ev) => {
    ev.preventDefault();
    this.setState({ showEditRegistryDetail: true })
  }

  handleGuestView = () => {
    // toast.success("Viewing registry as guest")
    this.props.dispatch(setguestViewRegistry(true))
  }

  handleDisableViewGuest = () => {
    this.props.dispatch(setguestViewRegistry(false))
  }

  COVER_PIC_OPTIONS = [
    {
      name: "Replace Background",
      action: () => {
        this.setState({ showCoverImagePicker: true })
      }
    }
  ]

  shareHandler = () => {
    // this.props.dispatch(setguestViewRegistry(false))
    this.setState({showShareRegistry: true})
  }


  render() {
    // const style = {
    //   backgroundImage: 'url(' + this.props.stateRegistryInfo.image.image + ')'
    // }
    return (
      <div data-tut="view-step-one">
        {this.props.stateGuestViewRegistry ?
          <div className="view-guest-header text-body py-2">
            <div className="container d-flex flex-wrap justify-content-between">
              <div className="d-flex align-items-center view-guest-link order-1 order-md-1 mb-2 mb-md-0" onClick={this.handleDisableViewGuest}>
                <i className="icon-chevron-left view-guest-text mr-2" />
                <a className="view-guest-text" tabindex="0">Back To View Registry</a>
              </div>
              <div className="view-guest-text order-3 order-md-2 ml-4 ml-md-0">This is how your guests will see your registry</div>
              <a className="view-guest-text view-guest-link order-2 order-md-3 mb-2 mb-md-0" onClick={() => this.shareHandler()}>Share</a>
            </div>
          </div> : null
        }
        <div className="view-registry-header" style={{ backgroundImage: 'url(' + this.props.stateRegistryInfo.image.image + ')' }}>

          {/*TODO: Change icon-preview to icon-camera when updated*/}
          {!this.props.stateGuestViewRegistry ?
            <div className="cover-pic-update-options">
              <div className="container">
                <div className="row">
                  <div className="col d-flex justify-content-end">
                    <MoreOption
                      data={this.COVER_PIC_OPTIONS}
                      iconClass="custom-icon-camera"
                      activeOptionClass="view-registry-cover-option-active"
                      buttonClass="update-cover-options p-1" />
                  </div>
                </div>
              </div>
            </div> : null
          }

          <div className="header-registry-detail">
            <div className="container my-4">
              <div className="row py-3 view-registry-header-border">
                <div className="col">
                  <div className="d-flex align-items-center justify-content-between justify-content-md-start">
                    <h2>{this.props.stateRegistryInfo.displayName ? this.props.stateRegistryInfo.displayName : ""}</h2>
                    {!this.props.stateGuestViewRegistry ?
                      <i className="icon-pencil ml-1 edit-name-icon" onClick={(ev) => this.changeRegistryDetail(ev)} />
                      : null
                    }
                  </div>
                  {this.props.stateRegistryInfo.greeting ?
                    <span
                      className="text-sm view-registry-header-greeting">{this.props.stateRegistryInfo.greeting}</span>
                    :
                    !this.props.stateGuestViewRegistry ?
                      <a className="add-greeting-link text-sm font-medium" href="#" tabindex="0"
                        onClick={(ev) => this.changeRegistryDetail(ev)}>Add a greeting</a> : null
                  }
                </div>
                {!this.props.stateGuestViewRegistry ?
                  <div className="col-12 col-md-6 header-buttons">
                    <div className="d-inline-block" data-tut="view-step-three">
                      <Button variant="secondary" disabled={this.props.hasNoProducts ? this.props.hasNoProducts : false} className="font-medium text-body py-1 px-3 mr-3 d-none d-md-inline-block "
                        onClick={this.handleGuestView}>View as Guest</Button>
                      <Button variant="primary" disabled={this.props.hasNoProducts ? this.props.hasNoProducts : false} className="font-medium text-body py-1 px-3 d-none d-md-inline-block"
                        onClick={() => this.setState({showShareRegistry: true})}>Share</Button>
                      <a className="view-and-share-link text-sm font-medium mr-3 d-inline-block d-md-none" href="#" tabindex="0"
                        onClick={this.handleGuestView}>View as Guest</a>
                      <a className="view-and-share-link text-sm font-medium d-inline-block d-md-none" href="#" tabindex="0"
                        onClick={() => this.setState({showShareRegistry: true})}>Share</a>
                    </div>
                  </div> : null
                }
              </div>
            </div>
          </div>
        </div>

        <CoverImagePicker
          show={this.state.showCoverImagePicker}
          closeCoverImagePicker={this.closeCoverImagePicker}
        />

        {this.state.showEditRegistryDetail ?
          <EditViewRegistryDetail
            show={this.state.showEditRegistryDetail}
            closeEditRegistryDetail={this.closeEditRegistryDetail}
            info={this.props.stateRegistryInfo}
          /> : null}

        <ShareRegistry
          show={this.state.showShareRegistry}
          closeShareRegistry={this.closeShareRegistry}
          registryInfo={this.props.stateRegistryInfo}
        />
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    stateRegistryInfo: state.registry.info,
    stateGuestViewRegistry: state.viewRegistry.guestViewRegistry,
    guestViewRegistryState: state.guestView.info,
  }
}


export default connect(
  mapStateToProps
)(withRouter(ViewRegistryHeader));